/* eslint-disable */
import React, { Fragment } from 'react';
import { Link } from 'react-router-dom';
import { loadDocs } from './utils/loadDocs';

import Header from './Header';

const HomePageDoc = () => (
  <Fragment>
    <Header />
    {loadDocs().map(component => (
      <div key={component.default.name} className="row">
        <Link to={`/${component.default.name}`}>
          {component.default.name.split(/(?=[A-Z])/).join(' ')}
        </Link>
        <component.default />
      </div>
    ))}
  </Fragment>
);

export default HomePageDoc;



