import React from 'react';
import { BrowserRouter } from 'react-router-dom';
import { renderRoutes } from 'react-router-config';
import Types from 'prop-types';
import homePageDoc from './HomePageDoc';
import singlePageDoc from './SinglePageDoc';
import { loadDocs } from './utils/loadDocs';

import './Docs.less';

const Root = ({ route }) => (
  <div className="container">
    {/* child routes won't render without this */}
    {renderRoutes(route.routes)}
  </div>
);
Root.propTypes = {
  route: Types.string.isRequired,
};

const routes = [
  {
    component: Root,
    routes: [
      {
        component: homePageDoc,
        exact: true,
        path: `/`,
      },
      // This is going to load the single page documentation.
      ...loadDocs().map(component => ({
        component: () => singlePageDoc(component),
        exact: true,
        path: `/${component.default.name}`,
      })),
    ],
  },
];

const Docs = () => <BrowserRouter>{renderRoutes(routes)}</BrowserRouter>;
export default Docs;



